.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.into-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.mask::after {
    content: " ";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.5;
}

.main {
    height: 100%;
    width: 100%;

}

.main .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.main .content h1 {
    font-size: 4rem;
    padding: .6rem 0 1.5rem;

}

.main .content p {
    font-size: 1.4rem;
    font-weight: 200;
    text-transform: uppercase;
}

.content .btn {
    margin: 1rem 1rem;
}

@media screen and(max-width:640px) {
    .main .content h1 {
        font-size: 3rem;
           }

    .main .content p {
        font-size: 1.4rem;
           }
}