  @import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@500&family=Outfit:wght@100;300;400;600&family=Pacifico&display=swap');

  * {
    box-sizing: border-box;
    margin: 0%;
    padding: 0%;
    font-family: 'Pacifico', cursive;
    font-family: 'Outfit', sans-serif;
  }

  body {
    background: #000;
  }

  h1,
  h4,
  p,
  a {
    color: aliceblue;
    text-decoration: none;
  }
  ul{
    list-style-type: none;
  }

  .btn{
    padding: 12px 32px;
    font-size: 1rem;
    text-transform: uppercase;
    background: rgb(248, 217, 15);
    color: #222;
    border: 1px solid #fff;
    font-weight: 600;
    cursor: pointer;
  }

  .btn-light{
    padding: 12px 32px;
    font-size: 1rem;
    font-weight: 600;
    cursor: pointer;
    background: transparent;
    border: 1px solid #fff;
    color: #fff;
  }

  .btn:hover{
    background:rgba(255, 255, 255, 0.2) ;
    color: #fff;
    transition: .3s;
  }

  